@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

:root {
  --main-color: #ff3e2b;
  --aux-color: #31a389;
  --aux-color-opacit: #31a389ee;
  --day-bg-color: #ffd16dee;
  --night-bg-color: #df7045dd;
  --aux-bg-color: #fafad4;
}

body, button { margin: 0; font-family: 'Fredoka One', sans-serif; box-sizing: border-box;  transition: all .5s; }

body.day { background-image: linear-gradient(to bottom, var(--day-bg-color) 55%, var(--aux-bg-color)); }
body.night { background-image: linear-gradient(to bottom, var(--night-bg-color) 20%, var(--aux-bg-color)) }

* { -ms-user-select: none; user-select: none; } /* deixar relogio nao selecionavel */
.hide { display: none; }

#root, .relogio { display: flex; justify-content: center; }
#root { height: 100vh; }
.relogio { flex-direction: column; }
.modulo { text-align: center; transition: font .2s ease-out; }

.btn {
  position: absolute;
  bottom: 10px;
  background-color: transparent;
  font-size: 1.7em;
  letter-spacing: 1px;
  padding: 8px;
  text-align: center;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
  border-width: 0;
  border-radius: 5px;
  color: var(--aux-bg-color);
}

/* RELOGIO DIGITAL ================================================================================== */

.digital {
  font-size: 7.5em;
  letter-spacing: 3px;
  word-spacing: -8px;
  color: var(--main-color);
}

.digital span { transition: all .3s ease .5s; opacity: 1; font-size: .9em; }
.digital .even { opacity: .7; }

.btn-ampm {
  left: 10px;
  width: 55px;
  background-color: var(--main-color);
}

/* RELOGIO LITERAL ================================================================================== */

.literal {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  font-size: 3em;
  line-height: 1.2em;
  word-spacing: 4px;
  letter-spacing: 2px;
  padding: 0 20px 0;
  height: 50px;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: var(--aux-color);
}

.btn-voice {
  right: 10px;
  width: 100px;
  background-color: var(--aux-color);
}

.btn-voice::before {
  width: 0;
  height: 0;
  top: -8px;
  left: 12px;
  background-color: transparent;
  border-bottom: 10px solid var(--aux-color);
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 0 solid transparent;
}

/* RELOGIO ANALOGICO ================================================================================== */

.analogico { /* mostrador */
  position: relative;
  left: 50%;
  border-radius: 50%;
  margin: 15px 0;
  transform: translateX(-50%);
  background: #fafad5 url(../images/bg_relog.png) center center / cover;
  border-radius: 50%;
  transition: width .2s ease-out, background .2s ease-out;
}

.analogico::before { /* circulo central */
  border-style: solid;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  z-index: 2;
  border-color: var(--aux-color);
  background-color: var(--aux-bg-color);
}

.analogico .ponteiro { position: absolute; top: 50%; left: 50%; border-radius: 50%; } /* container dos ponteiros */

.analogico .ponteiro::before { /* os ponteiros */
  border-radius: 7px 7px 0 0;
  left: 50%;
  bottom: 50%;
  transform: translateX(-50%);  
  background-image: linear-gradient(to top, var(--aux-color), var(--aux-color-opacit));
}

.analogico .minuto::before { height: 43%; }
.analogico .hora::before { height: 29%; }

*::before { content: ""; position: absolute; }

/* RESPONSIVIDADE ================================================================================== */

@media (orientation: landscape) and (min-width: 768px) {
  body { font-size: 10px; }
  .tam-relogio { width: 400px; min-height: 400px; }
  .analogico { box-shadow: 0 15px 0 var(--aux-color); margin-bottom: 25px; }
  .analogico .ponteiro::before { width: 15px; }
  .analogico:before { width: 10px; height: 10px; border-width: 10px; }
  .btn:hover { opacity: 0.7; }
}

@media (orientation: portrait) and (min-width: 500px) and (max-width: 767px), (orientation: landscape) and (min-height: 550px) and (max-width: 767px) {
  body { font-size: 8px; }
  .tam-relogio { width: 280px; min-height: 280px; }
  .analogico { box-shadow: 0 11px 0 var(--aux-color); margin-bottom: 15px; }
  .analogico .ponteiro::before { width: 11px; }
  .analogico:before { width: 8px; height: 8px; border-width: 7px; }
}

@media (orientation: portrait) and (max-width: 499px), (orientation: landscape) and (max-height: 549px) {
  body { font-size: 8px; }
  .tam-relogio { width: 250px; min-height: 250px; }
  .analogico { box-shadow: 0 9px 0 var(--aux-color);  margin-bottom: 12px; }
  .analogico .ponteiro::before { width: 10px; }
  .analogico:before { width: 8px; height: 8px; border-width: 7px; }
}

@media (max-width: 299px), (max-height: 299px) { 
  body { opacity: .3; overflow: hidden; }
}
